/* @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap'); */
:root {
  --primary: #77f8e0;
  --primaryhover: #53ad9c;
  --secondary: #cde8e1;
  --secondaryhover: #8fa29d;
  --crop-rect-outer-color: #32bfa9;
}

html {
  overflow: hidden;
}

.js-root-container {
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 10000;
  background: #000;
  top: 0px;
  left: 0px;
  box-sizing: border-box;
}

.imageviewer-container {
  position: absolute;
  z-index: 10;
  top: 0;
  width: 100%;
  height: 100%;
}

.imageviewer-warning-container {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.imageviewr-container-warning-text {
  color: #fff;
  font-size: 17px;
  line-height: 22px;
}

.camera-root {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.video-container {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: relative;
  overflow-y: clip;
}

.confirm-document-menu {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 41.5px;
  display: flex;
  justify-content: space-between;
  z-index: 1050;
  padding-left: 20px;
  padding-right: 19px;
  box-sizing: border-box;
}

.crop-rect-root {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  touch-action: none;
}

.crop-rect-border {
  z-index: 100;
  border: 2px solid #fff;
}
.crop-rect-outer-border-color {
  background: var(--crop-rect-outer-color);
}

.crop-rect-touch-anchor-tl {
  position: absolute;
  z-index: 200;
  top: -2%;
  left: -2%;
  background: transparent;
  height: 10%;
  width: 10%;
  cursor: nwse-resize;
}

.crop-rect-touch-anchor-tr {
  position: absolute;
  z-index: 200;
  top: -2%;
  right: -2%;
  background: transparent;
  height: 10%;
  width: 10%;
  cursor: nesw-resize;
}

.crop-rect-touch-anchor-br {
  position: absolute;
  z-index: 200;
  bottom: -2%;
  right: -2%;
  background: transparent;
  height: 10%;
  width: 10%;
  cursor: nwse-resize;
}

.crop-rect-touch-anchor-bl {
  position: absolute;
  z-index: 200;
  bottom: -2%;
  left: -2%;
  background: transparent;
  height: 10%;
  width: 10%;
  cursor: nesw-resize;
}

.shutter-sidebar-layout-landscape {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 65px;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
}

.shutter-sidebar-layout-portrait {
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: 65px;
  width: 100%;
  background: rgba(0, 0, 0, 0.25);
}

.shutter-sidebar {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.shutter-button-root {
  width: 65px;
  height: 42px;
  cursor: pointer;
}

.text-button {
  background: #333;
  color: var(--primary);
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  width: max-content;
  height: 22px;
  border: none;
  background-color: transparent;
  padding: 0px;
  font-size: 17px;
  line-height: 22px;
  cursor: pointer;
  letter-spacing: -0.408px;
  font-feature-settings: 'case' on;
}

.thumbnail-layout {
  position: absolute;
  bottom: 10px;
  left: 16px;
}

.snackbar-layout {
  /* Auto layout */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 14px 16px;
  gap: 4px;
  position: absolute;
  width: 344px;
  height: 48px;
  left: calc(50% - 172px);
  bottom: 52px;
  background: #2d3130;
  /* M3/Elevation Light/3 */
  box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15),
    0px 1px 3px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  box-sizing: border-box;
}

.snackbar-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.25px;
  /* M3/sys/light/inverse-on-surface */
  color: #eff1ef;
  /* Inside auto layout */
  flex: none;
  order: 0;
  flex-grow: 1;
}

.save-button-camera {
  border: 0px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 8px;
  cursor: pointer;

  position: absolute;
  width: 134px;
  height: 40px;
  right: 87px;
  bottom: 17px;
  background: var(--secondary);
  border-radius: 100px;

  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.1px;
  color: #06201b;
}

@media (pointer: fine) {
  .save-button-camera:hover {
    background: var(--secondaryhover);
  }
  .fab:hover {
    background: var(--primaryhover);
  }
  .icon-button:hover {
    background-color: rgba(0, 0, 0, 0.04);
  }
  .color-icon-button:hover {
    background: var(--secondaryhover);
  }
}

.fab {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  outline: 0;
  border: 0;
  margin: 0;
  border-radius: 0;
  padding: 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  -moz-appearance: none;
  -webkit-appearance: none;
  -webkit-text-decoration: none;
  text-decoration: none;
  color: inherit;
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  min-height: auto;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-radius: 24px;
  padding: 0 16px;
  min-width: 48px;
  width: auto;
  height: 48px;
  z-index: 1050;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.87);
  background: var(--primary);
  border-radius: 16px;
  position: fixed;
  bottom: 16px;
  right: 16px;
}
.icon-button {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
  outline: 0;
  border: 0;
  margin: 0;
  border-radius: 0;
  padding: 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  -moz-appearance: none;
  -webkit-appearance: none;
  -webkit-text-decoration: none;
  text-decoration: none;
  color: inherit;
  text-align: center;
  -webkit-flex: 0 0 auto;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  font-size: 1.5rem;
  padding: 8px;
  border-radius: 50%;
  overflow: visible;
  color: rgba(0, 0, 0, 0.54);
  -webkit-transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.color-icon-button {
  background: var(--secondary);
  width: 40px;
  height: 40px;
}

.lib_loading_spinner_curtain {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}

.lib_loading_spinner_container {
  position: fixed;
  top: calc(50% - 80px);
  left: calc(50% - 70px);
  width: 140px;
  height: 160px;
  /* background-color: rgba(0, 0, 0, 0.33); */
  /* border-radius: 8px; */
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.lib_loading_spinner {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lib_loading_spinner_text {
  position: absolute;
  left: 0;
  bottom: 4;
  width: 100%;
  text-align: center;
  color: #fff;
}
.lib_loading_spinner:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin-left: 6px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lib_loading_spinner 1.2s linear infinite;
}
@keyframes lib_loading_spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.cursor-pointer {
  cursor: pointer;
}
